/* --------------------------------------------------------------
 * = reset ,  normalize 
 * ----------------------------------------------------------- */

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-rendering: optimizelegibility;
}

/*  */
body, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, th, td, hr, button, article, aside, details,
figcaption, figure, footer, header, menu, nav, section {
  margin: 0;
  padding: 0;
}

/*  font  */
.pure-g [class*="pure-u"] {
  font-family: @font-family-sans-serif;
}

/* Firefox  */
acronym, abbr {
  border-bottom: 1px dotted;
  font-variant: normal;
}

/*  */
abbr {
  cursor: help;
}

/*  del  */
del {
  text-decoration: line-through;
}

address, caption, cite, code, dfn, em, th, var {
  font-style: normal;
  font-weight: 400;
}

/* , li  */
ul, ol {
  list-style: none;
}

/* ,  */
caption, th {
  text-align: left;
}

q:before, q:after {
  content: '';
}

:root sub, :root sup {
  vertical-align: baseline; /* for ie9 and other modern browsers */
}

/*  */
ins, a {
  text-decoration: none;
}

/*  u  html5 Draft
 *  class="typo-u" 
 *  <u> http://www.whatwg.org/specs/web-apps/current-work/multipage/text-level-semantics.html#the-u-element
 *  4 http://www.w3.org/TR/html401/appendix/changes.html#idx-deprecated
 *  <u> http://html5doctor.com/u-element/
 */
u {
  text-decoration: underline;
}

/*  */
mark {
  padding: 2px;
  margin: 0 5px;
}

/*  */
small,
figcaption {
  font-size: 0.9em;
  color: #888;
}

strong, b {
  font-weight: bold;
  color: @gray-dark;
}

/*  */
embed,
iframe,
object {
  max-width: 100%;
}

/*  */
[draggable] {
  cursor: move;
}

/*  */
.textwrap, .textwrap td, .textwrap th {
  word-wrap: break-word;
  word-break: break-all;
}

.textwrap-table {
  table-layout: fixed;
}

/* -----------------------------------------------------------
 * = 
 * ----------------------------------------------------------- */
.entry-content {
  position: relative;
  font-weight: 300;

  p, ul, ol {
    line-height: @line-height-base;
  }

  /* / */
  p, pre, ul, ol, dl, form, hr, table {
    margin-bottom: 1.2em
  }

  blockquote {
    padding: 0 15px;
    font-size: 16px;
  }

  /* margin  */
  h1, h2, h3, h4, h5, h6 {
    margin-top: 1.2em;
    margin-bottom: 0.6em;
    font-weight: 600;
    line-height: @line-height-base;
    font-family: @font-family-serif;
  }

  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.8em;
  }

  h3 {
    font-size: 1.6em;
  }

  h4 {
    font-size: 1.4em;
  }

  h5, h6 {
    font-size: 1.2em;
  }

  /*  ul  ol  */
  ul {
    margin-left: 1.3em;
    list-style: disc;
  }

  ol {
    list-style: decimal;
    margin-left: 1.9em;
  }

  li ul, li ol, ul, ol {
    margin-bottom: 0.8em;
    margin-left: 2em;
  }

  li ul, li ol {
    list-style: circle;
  }

  /*  ul/ol table  */
  table th, table td, th, td, table caption {
    border: 1px solid #ddd;
    padding: 0.5em 1em;
    color: #666;
  }

  table th, th {
    background: #fbfbfb;
  }

  table thead th, thead th {
    background: #f1f1f1;
  }

  table caption {
    border-bottom: none;
  }

  /*  webkit  input  textarea   */
  input, textarea {
    border-radius: 0;
  }

  em, legend, caption {
    font-weight: inherit;
  }

}